import Api from '@/services/Api'

export default {
  register (credentials) {
    return Api().post('register', credentials)
  },

  login (credentials) {
    return Api().post('login', credentials)
  },

  fetchLinkToken () {
    return Api().post('/link/token/create')
    
  },

  ExchangeToken () {
    return Api().post('/item/public_token/exchange')
  }
}